@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@48,400,0,0');

.green {
  color: yellowgreen;
}

.red {
  color: orangered;
}

.hidden {
  visibility: hidden;
}

.wrapper {
  height: 96.8vh;
  border-radius: 6px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.add-text {
  font-weight: bold;
  display: block;
  font-size: 14px;
  text-align: center;
}

.button_back,
.arrow_showStats {
  margin: 5px auto;
  cursor: pointer;
}

.arrow_showStats {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 16px;
  background: transparent;
  border: none;
  margin: -5px 0;
  padding: 3px;
  position: relative;

  span {
    position: absolute;
    top: 15%;
  }

  &:hover,
  &:active {
    color: yellowgreen;
    background: transparent;
    border: none;
    box-shadow: none;
  }
}

.history {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
    scrollbar-width: none;
  }
}

.message {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  color: red;
  font-size: 14px;
  margin: 20px;
}


